import React from 'react';
import { Link } from 'react-router-dom';
import HomeIcon from '@material-ui/icons/Home';
import MenuBookIcon from '@material-ui/icons/MenuBook';
import CheckIcon from '@material-ui/icons/Check';
import ClearIcon from '@material-ui/icons/Clear';
import { AppConsumer } from '../../../context';
import './SideDrawer.css';

const sideDrawer = props => {
  let drawerClasses = 'side-drawer';
  if (props.show) {
    drawerClasses = 'side-drawer open';
  }
  return (
    <AppConsumer>
      {value => (
        <nav className={drawerClasses} onClick={() => value.backdropClickHandler()}>
          <div className="sidebar-header">
            {
              value.auth ? <CheckIcon style={{color: "green"}} /> 
              : <ClearIcon style={{color: "red", padding: 0}} />
            }{" "}
            <p style={{color: "white", padding: 0}}>{value.auth ? value.username : null}</p>
            
          </div>
          <ul>
            <li>
              <Link className="nav-link" to="/">
                <p>Home </p>
                <HomeIcon color="inherit" />
              </Link>
            </li>
            <li>
              <Link className="nav-link" to="/blog-list/:id">
                <p>Blogs </p>
                <MenuBookIcon color="inherit" />
              </Link>
            </li>
            <li>
              <Link className="nav-link" to="/join">
                <p>Join </p>
              </Link>
            </li>
            {/* <li>
              <Link className="nav-link" to="/login">
                <p>Login </p>
              </Link>
            </li> */}
            <li>
              <div className="nav-link">
              
              {/* if user is authorised return Check else Clear */}
                
                  
                
              </div>
            </li>
          </ul>
        </nav>
      )}
    </AppConsumer>
  );
}

export default sideDrawer;
