import React from 'react';
import { Route, Switch } from 'react-router-dom';

import NavBar from './components/nav/Navbar/Navbar';
import SideDrawer from './components/nav/SideDrawer/SideDrawer';
import HomePage from './pages/home-page/HomePage';
import BlogListPage from './pages/blog-list-page/BlogListPage';
import BlogPage from './pages/blog-page/BlogPage';
import PageNotFound from './pages/page-not-found/PageNotFound';
import PrivateRoute from './components/private-route/PrivateRoute';
import Login from './pages/login-page/Login';
import Join from './pages/join-page/Join';
import { AppConsumer } from './context';

function App() {
  return (
    <AppConsumer>
      {value => (
        <div className="App"> 
          <NavBar drawerClickHandler={value.drawerToggleClickHandler} />
          <SideDrawer show={value.sideDrawerOpen} />
          <Switch>
            <Route path="/" exact component={HomePage} />
            <Route path="/blog-list/:id" component={BlogListPage} />
            <Route path="/login" component={Login} />
            <Route path="/join" component={Join} />
            <PrivateRoute exact path="/blog/:name" component={BlogPage} auth={value.auth} />
            <Route component={PageNotFound} /> 
          </Switch>
        </div>
      )} 
    </AppConsumer>
  );
}

export default App;