import React, { useState, useEffect } from 'react';

import blogContent from '../../data/blog-content';
import BlogPageHeader from './BlogPageHeader';
import BlogPageContent from './BlogPageContent';
import AddLikes from './AddLikes';
// import BlogPageFeedbackForm from './BlogPageFeedbackForm';
// import BlogPageFeedback from './BlogPageFeedback';
import BlogList from '../../components/blog-list/BlogList';
import PageNotFound from '../page-not-found/PageNotFound';

const BlogPage = ({ match }) => {
  
  // match is an automatic prop from react-router
  const name = match.params.name;

  // return the content of the current blog
  const blog = blogContent.find(blog => blog.name === name);

  // return all blogs except the current blog
  const otherBlogs = blogContent.filter(blog => blog.name !== name);

  const image = blog.image;

  // State
  const [blogInfo, setBlogInfo] = useState({
    likes: 0,
    dislikes: 0,
    // comments: []
  });

  useEffect(() => {
    const fetchData = async () => {
      const result = await fetch(`/api/blogs/${name}`);
      const body = await result.json();
      // console.log(body);
      setBlogInfo(body);
    }
    fetchData();
  }, [name]);

  // if not a blog return 404 
  if (!blog) return <PageNotFound />
  
  return (
    <>
      <BlogPageHeader data={blogInfo} />
      <BlogPageContent blog={blog} image={image}/>
      <AddLikes data={blogInfo} setBlogInfo={setBlogInfo} />
      {/*  
        <BlogPageFeedbackForm blogName={name} setBlogInfo={setBlogInfo} />
        <BlogPageFeedback comments={blogInfo.comments} /> 
      */}
      <BlogList blogs={otherBlogs} />
    </>     
  );
}

export default BlogPage;
