import React from 'react'
// import reactLogo from '../../svgs/reactjs-ar21.svg';
// import expressLogo from '../../svgs/expressjs-ar21.svg';
import LikeBtn from '../../components/LikeBtn';
import DislikeBtn from '../../components/DislikeBtn';
import './styles.css';

const AddLikes = ({ data, setBlogInfo }) => {

  const addLike = async () => {
    const result = await fetch(`/api/blogs/${data.name}/likes`, {
      method: 'post',
    });
    const body = await result.json();
    setBlogInfo(body);
  }

  const addDislike = async () => {
    const result = await fetch(`/api/blogs/${data.name}/dislikes`, {
      method: 'post',
    });
    const body = await result.json();
    setBlogInfo(body);
  }
  
  return (
    <div className="AddLikes"> 
      {/* 
        <img className="react-logo3" src={reactLogo} alt="react svg" />          
        <h2>So... what do you think?</h2>
        <img className="express-logo2" src={expressLogo} alt="express svg" />
        <h2>Yourself</h2> 
      */}
      <div className="like-btn-box">
        <LikeBtn data={data} addLike={addLike} />
        <DislikeBtn data={data} addDislike={addDislike} />
      </div>
    </div>
  );
}

export default AddLikes;

