import React from 'react';
import { Link } from 'react-router-dom';
import HomeIcon from '@material-ui/icons/Home';
import MenuBookIcon from '@material-ui/icons/MenuBook';
import CheckIcon from '@material-ui/icons/Check';
import ClearIcon from '@material-ui/icons/Clear';
import DrawerToggleButton from '../SideDrawer/DrawerToggleButton';
import reactLogo from '../../../svgs/reactjs-ar21.svg';
import mongodbLogo from '../../../svgs/mongodb-ar21.svg';
import { AppConsumer } from '../../../context';
import './Navbar.css';

const Navbar = (props) => (
  
  <AppConsumer>
    {value => (
      <header className="toolbar">
        <nav className="toolbar__navigation">
          <div className="toolbar__toggle-button">
            <DrawerToggleButton click={props.drawerClickHandler} />
          </div>
          <div className="toolbar__logo">
            <img style={{height: "50px", paddingBottom: "6px"}} src={reactLogo} alt="react svg" />
          </div>
          <div className="spacer" />
          <div className="toolbar_navigation-items">
            
            <ul>
              <img style={{height: "60px", width: "100px", paddingTop: "7px"}} src={mongodbLogo} alt="mongodb svg" />
              <li>
                <div className="nav-link">
                  {value.auth ? value.username : null}
                  {" "}
                  {
                    value.auth ? <CheckIcon style={{color: "gold"}} /> 
                    : <ClearIcon style={{color: "red"}} />
                  }
                </div>
              </li>
              <li>
                <Link className="nav-link" to="/">
                  <p>Home </p>
                  <HomeIcon color="inherit" />
                </Link>
              </li>
              <li>
                <Link className="nav-link" to="/blog-list/:id">
                  <p>Blogs </p>
                  <MenuBookIcon color="inherit" />
                </Link>
              </li>
              <li>
                <Link className="nav-link" to="/join">
                  <p>Join</p>
                </Link>
              </li>
              {/* <li>
                <Link className="nav-link" to="/login">
                  <p>Login</p>
                </Link>
              </li> */}
            </ul>
          </div>
        </nav>
      </header>
    )}
  </AppConsumer>
);

export default Navbar;