import React from 'react';

const BlogPageContent = ({ blog, image }) => {
  return (
    <div className="content">
      {blog.content.map((text, key) => (
        <div className="blog-page-data" key={key}> 
          <img className="blog-page-img" src={image[0]} alt="" />
          <p className="blog-page-text">{text}</p>
          <img className="blog-page-img" src={image[1]} alt="" />
          <p className="blog-page-middle-text">{blog.moreContent}</p>
          <img className="blog-page-img" src={image[2]} alt="" />
          <p className="blog-page-bottom-text">{blog.moreContentAgain}</p>
        </div>
      ))}
    </div>
  );
}

export default BlogPageContent;
