import React, { Component } from 'react'
import blogContent from './data/blog-content'

const data = blogContent

// to check if an email is entered in JoinForm 
const emailRegex = RegExp(
  /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
)

// to check a set criteria in JoinForm
const formValid = ({ formErrors, ...rest }) => {

  //set to true so that if no errors occur return valid
  let valid = true
  
  //if val.length is greater than 0 it means there's an error msg
  Object.values(formErrors).forEach(val => {
    val.length > 0 && (valid = false)
  })

  Object.values(rest).forEach(val => {
    val === null && (valid = false)
  })

  return valid
}

// this is the equivalent to the createStore method of Redux
const AppContext = React.createContext();

class AppProvider extends Component {
  constructor(props) {
    super(props)
    this.state = {
      // content
      data,

      // fakeAuth
      auth: false,

      // navbar
      sideDrawerOpen: false,

      // form validation  
      email: null,
      username: null,
      password: null,
      confirmPassword: null,
      formErrors: {
        email: "",
        username: "",
        password: "",
        confirmPassword: ""
      },
      msg: "Join",

    }
  }

  // JoinPage form submission function
  handleSubmit = (e, formValid) => {
    e.preventDefault();
    
    if(formValid(this.state)) {
      console.log(`
        --SUBMITTING--
        User: ${this.state.username}
        Email: ${this.state.email}
        Password: ${this.state.password}
      `);
      
      this.setState({auth: true});
      // this.setState(
      //   {msg: `${this.state.username} your've been validated`}
      // );

    } else {
      console.error("FORM INVALID - DISPLAY ERROR MESSAGE");
    }
  }

  // JoinPage function to check for input errors
  handleChange = e => {
    e.preventDefault()
    const { name, value } = e.target
    let formErrors = this.state.formErrors

    switch (name) {
      case 'username':
        formErrors.username = 
          value.length < 3 ? 'Minimum 3 characters required' : ""
        break  
      case 'email':
        formErrors.email = 
          emailRegex.test(value) ? "" : "Invalid email address"
        break 
      case 'password':
        formErrors.password = 
          value.length < 6 ? 'Minimum 6 characters required' : ""
        break 
      case 'confirmPassword':
        formErrors.confirmPassword = 
          value !== this.state.password  ? 'Passwords do not match!' : ""
        break
      default:
        break;
    }
    this.setState({ formErrors, [name]: value});
    
  }

  // nav functions
  drawerToggleClickHandler = () => {
    this.setState((prevState) => {
      return {sideDrawerOpen: !prevState.sideDrawerOpen};
    });
  };

  backdropClickHandler = () => {
    this.setState({sideDrawerOpen: false});
  };

  // function for if successful form validation
  fakeAuth = (auth) => {
    return({
      fakeAuth: auth ? "block" : "fakeAuth"
    });
  }

  render() {
    
    return (
      <AppContext.Provider
        value={{
          ...this.state,
          fakeAuth: this.fakeAuth,
          drawerToggleClickHandler: this.drawerToggleClickHandler,
          backdropClickHandler: this.backdropClickHandler,
          handleChange: this.handleChange,
          handleSubmit: this.handleSubmit,
          formValid
          
        }}
      >
        {this.props.children}
      </AppContext.Provider>
    );
  }
}

const AppConsumer = AppContext.Consumer;
export { AppProvider, AppConsumer }
