import React from 'react';
import { Link } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import BlogListCards from '../blog-list-cards/BlogListCards';

/* 
  This components purpose is to map the data passed as props,
  rendering the BlogListCards onto a grid for a responsive display
*/

const BlogList = ({ blogs }) => {
  return (
    <div className="blogsList">
      <Grid container spacing={10} style={{padding: '24px'}}>
        {blogs.map((blog, key) => (
          <Grid 
            key={key} 
            item
            xs={12} 
            sm={6} 
            md={4} 
            lg={4} 
            xl={3} 
          >
            <Link 
              className="blog-list-item"
              key={key} 
              to={`/blog/${blog.name}`}
            >
            <BlogListCards 
              name={blog.name}
              content={blog.content[0].substring(0, 150) + ' ...'} 
              image={blog.image}
              key={key} 
            />
            </Link>
          </Grid> 
        ))}
      </Grid>
    </div>
  );
}

export default BlogList;